// 费用明细弹框
var PriceDetail = React.createClass({
  getInitialState: function(){
    return{
      items: []
    }
  },
  closeFeiYong:function(){
    this.props.closeFeiYong()
  },
  render:function(){
    var reserveNums=this.props.reserveNum;
    var starttime=uddh5.location.queryKey('starttime'),
      endtime=uddh5.location.queryKey('endtime'),
      dDay=(!!starttime && !!endtime)? uddh5.date.gitDiffDay(endtime,starttime) : '';
    if(reserveNums<=0){
      return;
    }else{
      for(var i=1; i<=reserveNums; i++){
        this.state.items.push(<li key={"feiyon"+i} className="clearfix"><span className="pull-left">费用{i}</span><span className="pull-right"><strong>￥{this.props.productPrice*dDay}</strong>x<em>1</em>人</span></li>)
      }
    }
     return(
          <div className="price_detail_cont">
              <div className="shade" onClick={this.closeFeiYong}></div>
              <div className="price_detail" ref="modalRoot">
                  <h5 className="titel">价格详情</h5>
                  <ul className="price_detail_list">
                    {this.state.items}
                  </ul>
              </div>
          </div>
      )
  }
});

var HotelOrder=React.createClass({
  getInitialState: function(){
    return{
      bookMethod: '',
      reserveNum: "1",
      liveName: [],
      mobile: '',
      yaoqiu: '',
      roomTypeName: '',
      hotelProduct: {},
      bedType: '',
      broadBandName: '',
      productPrice: '',
      issell: '',
      resourceId: '',
      orderAmount: '',
      productName: '',
      hotelName: '',
      picurl: '',
      quhao: '+86',
      showModal: false,
      uuid: '',
      opened: false,
      sheshi: [],
      includingbreakfastNane: '',
      disabled: false,
      messageShow: false,
      textMessage: '',
    }
  },
  getReserveNum: function(obj){
    if(!obj.reserveNum){
      console.log(obj.reserveNum);
      return;
    }
    this.setState({
      reserveNum: obj.reserveNum
    })
  },
  getLiveName: function(obj){
    if(!obj.liveName){
      return;
    }
    this.setState({
      liveName: obj.liveName.length>this.state.reserveNum? obj.liveName.splice(this.state.reserveNum) : obj.liveName
    })
  },
  getMobile: function(obj){
    if(!obj.mobile){
      return;
    }
    this.setState({
      mobile: obj.mobile
    })
  },
  getYaoQiu: function(obj){
    if(!obj.yaoqiu){
      return;
    }
    this.setState({
      yaoqiu: obj.yaoqiu
    })
  },
  getQuhao: function(obj){
    this.setState({
      quhao: obj.quhao
    })
  },
  getuuid: function(obj){
    this.setState({
      uuid: obj.uuid
    });
  },
  closeAlert: function(){
    this.setState({
      messageShow: false
    });
  },
  openAlert: function(text, duration){
    var that = this;
    var duration= duration == undefined? 2000 : duration;
    this.setState({
      messageShow: true,
      textMessage: text
    });
    setTimeout(function() {
      that.closeAlert();
    }, duration);
  },
  handleSubmit: function(e){
    e.preventDefault();
    var hotelid = uddh5.location.queryHash().split('/')[2];
    var roomid=uddh5.location.queryHash().split('/')[3];
    var productid=uddh5.location.queryHash().split('/')[4];
    if(this.state.reserveNum == ''){
      this.openAlert("房间数量不能为空！");
      return;
    }
    if(this.state.liveName == ''){
      this.openAlert("入住人不能为空！");
      return;
    }
    if($.grep(this.state.liveName.slice(0,this.state.reserveNum), function (elem, index) { return elem != undefined && elem != ''; }).length < this.state.reserveNum){
      this.openAlert("入住人不能为空！");
      return;
    }
    if(this.state.mobile == ''){
      this.openAlert("手机号不能为空！");
      return;
    }
    if(!((/^1[0-9]\d{9}$/).test($.trim(this.state.mobile)))){
      this.openAlert("手机号不合法！");
      return;
    }
    var starttime=uddh5.location.queryKey('starttime'),
      endtime=uddh5.location.queryKey('endtime'),
      dDay=(!!starttime && !!endtime)? uddh5.date.gitDiffDay(endtime,starttime) : '';
    var forms={productId: productid, resourceId: this.state.resourceId, hotelId: hotelid, roomTypeId: roomid, userId: uddh5.getCookie('userId'), orderAmount: (this.state.productPrice)*$.trim(this.state.reserveNum)*dDay, payAmount: (this.state.productPrice)*$.trim(this.state.reserveNum)*dDay, reserveNum: this.state.reserveNum, liveName: this.state.liveName.join(','), mobile: this.state.mobile, checkInDate: starttime, checkOutDate: endtime, email: 'uddtrip@uddtrip.com', productName: this.state.productName, hotelName: this.state.hotelName, roomName: this.state.roomTypeName, uuid: this.state.uuid};
    console.log(forms);
    // if(isPay){
    //   this.togglePayNative();
    // }else{
    //   this.toggleNative();
    // }
    // /stockType是否现询(1是0不是)
    $.post("/addapphotelorder", forms, function(data){
      this.setState({
        disabled: true
      });
      if(data.code ==1){
        var data=data.data;
        data.stockType == 0? this.togglePayNative(data) : this.toggleNative(data)
      }else{
        this.openAlert("提交订单失败！");
      }
    }.bind(this));
  },

  //去支付
  togglePayNative: function(obj){
    //与app的交互支付
    var requestHybrid={
      tagname: 'forward',
      topage:'order',
      type:"native",
      param: {
        typeId: "10119",
        stockType: obj.stockType,
        orderId: obj.productOrderId
      }
    }
    var native_callback=function(data){
    }
    uddh5.bridge(native_callback,requestHybrid);
  },
  //去咨询
  toggleNative: function(obj){
    var requestHybrid={
      tagname: 'forward',
      topage:'order',
      type:"native",
      param: {
          typeId: "10118",
          stockType: obj.stockType,
          orderId: obj.productOrderId
      }
    }
    var native_callback=function(data){
    }
    uddh5.bridge(native_callback,requestHybrid);
  },
  componentDidMount: function(){
    var hotelid = uddh5.location.queryHash().split('/')[2];
    var roomid=uddh5.location.queryHash().split('/')[3];
    var productid=uddh5.location.queryHash().split('/')[4];
    var sheshis=[];
    $.post('/gethotelproductinfo', {hotelId:hotelid, roomTypeId:roomid, productId:productid, startDate:uddh5.location.queryKey('starttime'),endDate:uddh5.location.queryKey('endtime')}, function(data){
      if(data.code == 1){
        var data=data.data;
        if(!!data.hotelProduct.includingbreakfastNane){
          sheshis.push({text: data.hotelProduct.includingbreakfastNane, icon: "icon-chazi"});
        }
        if(!!data.buildingArea){
          sheshis.push({text: data.buildingArea+"平方米", icon: "icon-pingfang"});
        }
        if(!!data.maxPerson){
          sheshis.push({text: data.maxPerson, icon: "icon-person"});
        }
        if(!!data.isAddBedName){
          sheshis.push({text: data.isAddBedName, icon: "icon-zushu"})//icon is none
        }
        if(!!data.bedSize){
          sheshis.push({text: data.bedSize, icon: "icon-zushu"})
        }
        if(!!data.broadBandName){
          sheshis.push({text: data.broadBandName, icon: "icon-wifi"})
        }
        if(!!data.bathRoomName){
          sheshis.push({text: data.bathRoomName, icon: "icon-shower"})
        }
        if(!!data.foodName){
          sheshis.push({text: data.foodName, icon: "icon-mianfeipingzhuangshui"})
        }
        if(!!data.mediaFacilitiesName){
          sheshis.push({text: data.mediaFacilitiesName, icon: "icon-fangjianwifi"})
        }
        if(!!data.facilitiesName){
          sheshis.push({text: data.facilitiesName, icon: "icon-110vdianyachazuo"})
        }
        if(this.isMounted()){
          this.setState({
            roomTypeName: !!data.roomTypeName.length? data.roomTypeName : '',
            hotelProduct: data.hotelProduct,
            bedType: data.bedType,
            broadBandName: data.broadBandName,
            productPrice: data.hotelProduct.productPrice,
            stockType: data.hotelProduct.stockType,
            resourceId: data.hotelProduct.resouceId,
            productName: data.hotelProduct.productName,
            picurl: data.picurl,
            hotelName: data.hotelName,
            issell: data.hotelProduct.issell,
            checkinTime: data.checkinTime,
            departureTime: data.departureTime,
            sheshi: sheshis
          });
        }
      }
    }.bind(this))
    //与app的交互
    var that=this;
    var requestHybrid={
       tagname: 'forward',
       topage:'order',
       type:"native",
       param: {
           typeId: "c10106",
           title:"酒店订单填写"
       }
    }
    var native_callback=function(data){
    }
    uddh5.bridge(native_callback,requestHybrid);
     
  },
  openModal: function(){
    this.setState({
      opened: true
    });
  },
  closeModal: function(){
    this.setState({
      opened: false
    });
  },
  openFeiYong: function(e){
    $(e.target).addClass('disabled');
    this.setState({
      showModal: true
    });
  },
  closeFeiYong: function(){
    $(this.refs.feiyongToggle).removeClass('disabled');
    this.setState({
      showModal: false
    })
  },
  render: function(){
    var stockType=!!(this.state.stockType);
    var starttime=uddh5.location.queryKey('starttime'),
      endtime=uddh5.location.queryKey('endtime'),
      dDay=(!!starttime && !!endtime)? uddh5.date.gitDiffDay(endtime,starttime) : '';
    return (
      <div>
        <HotelHeader roomTypeName={this.state.roomTypeName} hotelProduct={this.state.hotelProduct} bedType={this.state.bedType} broadBandName={this.state.broadBandName} openModal={this.openModal} getuuid={this.getuuid}/>
        <form onSubmit={this.handleSubmit} className="hotel-forms-order">
          <OrderBooker getReserveNum={this.getReserveNum} getLiveName={this.getLiveName} getMobile={this.getMobile} getQuhao={this.getQuhao} resourceId={this.state.resourceId} hotelName={this.state.hotelName} openAlert={this.openAlert}/>
          <BuChong getYaoQiu={this.getYaoQiu} />
          <FooterPlaceholer />
          <footer className="clearfix">
            <div className="total pull-left">总额：￥<strong>{(this.state.productPrice)*$.trim(this.state.reserveNum)*dDay}</strong>
            </div>
            <div className="btns pull-right">
              <a ref="feiyongToggle" href="javascript:void(0)" onClick={this.openFeiYong}>费用明细</a>
              {this.state.disabled? <button className="btn btn-orange" type="submit" disabled style={{backgroundColor: "#bbb"}}>{stockType? '去预定' : '去支付'}</button> : <button className="btn btn-orange" type="submit">{stockType? '去预定' : '去支付'}</button>}
            </div>
          </footer>
        </form>
        {this.state.opened? <ModalRoom picurl={this.state.picurl} closeModal={this.closeModal} checkinTime={this.state.checkinTime} sheshi={this.state.sheshi} departureTime={this.state.departureTime}/> : ''}
        {this.state.showModal? <PriceDetail productPrice={this.state.productPrice} reserveNum={this.state.reserveNum} closeFeiYong={this.closeFeiYong}/> : ''}
        {this.state.messageShow? <Alert textMessage={this.state.textMessage}/> : ''}
      </div>
    );
  }
})
export default HotelOrder


